<template>
	 <div class="three">
	 	<div class="box_2">
    	<div class="content-box">
    		<h4>绑定提现银行卡</h4>
    		<div class="content">
    			<ul>
    				<li class="clearfix">
    					<div class="one">   </div>
    					<div class="two">信用卡、国际卡、部分存折卡不能提现</div>
    				</li>
    				<li class="clearfix">
    					<div class="one">
    						<span>*</span>
    						银行卡类型：
    					</div>
    					<div class="two">储蓄卡（借记卡）</div>
    				</li>
    				<li class="clearfix">
    					<div class="one">
    						<span>*</span>
    						真实开户人姓名：
    					</div>
    					<div class="two">
    						<input type="text" class="a" />
    					</div>
    				</li>
    				<li class="clearfix">
    					<div class="one">
    						<span>*</span>
    						选择银行：
    					</div>
    					<div class="two">
    						  <el-select v-model="value" placeholder="招商银行">
							    <el-option
							      v-for="item in options"
							      :key="item.value"
							      :label="item.label"
							      :value="item.value">
							    </el-option>
							  </el-select>
    					</div>
    				</li>
    				<li class="clearfix">
    					<div class="one">
    						<span>*</span>
    						选择地区：
    					</div>
    					<div class="two">
    						<el-select v-model="value" placeholder="吉林">
							    <el-option
							      v-for="item in optionss"
							      :key="item.value"
							      :label="item.label"
							      :value="item.value">
							    </el-option>
							  </el-select>
    					</div>
    				</li>
    				<li class="clearfix">
    					<div class="one">
    						<span>*</span>
    						银行卡号：
    					</div>
    					<div class="two">
    						<div class="box">
    							<input type="text" />
    						</div>
    					</div>
    				</li>
    			</ul>
    			<div class="tet">
    			<p>此银行的开户名必须为</p><span class="a"></span><p>,否则提现会失效。</p>
    			</div>
    			
    		</div>
    		<router-link to="/four"><el-button type="danger" style="margin-top: 12px; margin-left: 182px;">保存账户</el-button></router-link>
    	</div>
	</div>
    </div>
</template>

<script>
import two from '@/dayun/child/tx'
  export default {
  	components:{
    	two
   },
    data() {
      return {
        options: [{
          value: '选项1',
          label: '招商银行'
        }, {
          value: '选项2',
          label: '工商银行'
        }, {
          value: '选项3',
          label: '农业银行'
        }, {
          value: '选项4',
          label: '建设银行'
        }, {
          value: '选项5',
          label: '农商银行'
        }],
         optionss: [{
          value: '选项1',
          label: '吉林'
        }, {
          value: '选项2',
          label: '山西'
        }, {
          value: '选项3',
          label: '山东'
        }, {
          value: '选项4',
          label: '河北'
        }, {
          value: '选项5',
          label: '河南'
        }],
        value: ''
      }
    }
  }
</script>

<style lang="less" scoped>
	.three{
		.box_2{
			width: 1600px;
			background: #fafbff;
			float: left;
			margin-left: 2px;
			color: #bbbbbb;
		.content-box{
			margin: 28px;
			width: 1200px;
			border:1px solid #e8e8f4;
			background: #fff;
			padding-bottom: 50px;
			height:921px;
			h4{
				font-weight: bold;
				padding: 16px 0px 12px 20px;
				margin: 0 ;
				border-bottom: 1px solid #d0d0d0;
			}
			.content{
				padding:50px 40px 45px 72px;
				ul{
					list-style:none;
					li{
						height:50px;
						div{
							float: left;
						}
						.one{
							width: 150px;
							height: 50px;
							text-align: right;
							span{
								color: red;
							}
						}
						.two{
							.a{
								width:78px;
								height: 22px;
								display: block;
								background-color: #4aacc6;
								border: 2px solid #3b8093;
								border-radius: 10px;
								}
								.box{
									width: 230px;
									height: 35px;
									border: 1px solid #d3d3d3;
									input{
										width:120px;
										height: 22px;
										display: block;
										background-color: #4aacc6;
										border: 2px solid #3b8093;
										border-radius: 10px;
										margin-top: 5px;
									}
								}
							
						}
					}
				}
				.tet{
							margin-left: 105px;
							font-size: 12px;
							color: #bbbbbb;
							p{
								float: left;
							}
							}
							.a{float: left;
								width:78px;
								height: 22px;
								display: block;
								background-color: #4aacc6;
								border: 2px solid #3b8093;
								border-radius: 10px;
								}
			}
		}
		}
	}
</style>


